import { Text, IconButton, Flex, Avatar, Spacer, Menu, MenuButton, MenuList, MenuItem } from '@chakra-ui/react';
import { useColorMode } from '@chakra-ui/react';
import LightModeIcon from '@mui/icons-material/LightMode';
import DarkModeIcon from '@mui/icons-material/DarkMode';
import MenuIcon from '@mui/icons-material/Menu';
import NavItem from './NavItem';

function NavBar (props) {
    const { colorMode, toggleColorMode } = useColorMode();
    
    return (
        <Flex sx={{ p: 1, alignItems: 'center', justifyContent: 'flex-start', bgColor: colorMode == 'dark' ? '#212124' : '#E2E5DE' }}>
            <Avatar
                sx={{ ml: 2, mt: 1}}
                size='sm'
                src='https://shoplineimg.com/59b2d9e49a76f018310010d2/5ed8f8404e55bf0045f352e7/800x.png?'
                display={{ base: 'none', lg: 'flex' }} 
            />
            <Menu>
                <MenuButton as={IconButton} icon={<MenuIcon />} ml='10px' display={{ base: 'flex', lg: 'none' }} />
                <MenuList>
                    <MenuItem>About</MenuItem>
                    <MenuItem>Project</MenuItem>
                    <MenuItem>Comment</MenuItem>
                </MenuList>
            </Menu>
            <NavItem text="About"/>
            <NavItem text="Project"/>
            <NavItem text="Comment"/>
            <Spacer />
            <IconButton
                size='md'
                borderRadius={50}
                onClick={toggleColorMode}
                icon={ colorMode == 'light' ? <DarkModeIcon/> : <LightModeIcon/> }
            />    
        </Flex>
    );
}

export default NavBar;